import React, { useEffect, useState } from "react";
import axios from "axios";
import { Trans, useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import Modal from "react-modal";

import { token, chat_id } from "../bot";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    backgroundColor: "rgba(0,0,0,1)",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

interface Props {
  className?: String;
}

Modal.setAppElement("#root");

function Contacts({ className }: Props) {
  let timer: NodeJS.Timeout;
  const { t } = useTranslation();
  const [a, setA] = useState<number | null>(null);
  const [b, setB] = useState<number | null>(null);
  const [name, setName] = useState("");
  const [message, setMessage] = useState("");
  const [captPassed, setCapt] = useState(false);
  const [captcha, setCaptcha] = useState("");
  const [modalIsOpen, setIsOpen] = useState(false);
  const [modalText, setModalText] = useState("");

  const generate = () => {
    setA(Math.floor(Math.random() * 100));
    setB(Math.floor(Math.random() * 100));
  };

  useEffect(() => {
    generate();

    return () => {
      clearTimeout(timer);
    };
  }, []);

  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value);
  };
  const handleChangeMessage = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setMessage(e.target.value);
  };
  const handleChangeCapt = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!a || !b) return;
    setCaptcha(e.target.value);
    if (a + b === +e.target.value) {
      setCapt(true);
    } else {
      setCapt(false);
    }
  };
  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (captPassed) {
      let text = `Отправил: *${name}*
      Текст: *${message}*
      Дата: *${new Date()}* `;
      const url = `https://api.telegram.org/bot${token}/sendMessage?chat_id=${chat_id}&text=${text}&parse_mode=markdown`;

      axios
        .get(url)
        .then(() => {
          setMessage("");
          setName("");
          setCaptcha("");
          setModalText("Success!");
          openModal();
          generate();
        })
        .catch((error) => {
          console.log(error);

          openModal();
          setModalText("Fail!");
        });
    }
  };

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <section id="contacts" className={`section  ${className ? className : ""}`}>
      <h5 className="section-title">
        <svg
          id="svg2"
          className="section-svg"
          width="100"
          height="16"
          viewBox="0 0 100 16"
        >
          <defs>
            <clipPath id="b5">
              <rect width="100" height="16" />
            </clipPath>
          </defs>
          <g id="a" clipPath="url(#b5)">
            <path
              d="M861,2550.928l11.181-11.181,11.181,11.181,11.181-11.181,11.181,11.181,11.181-11.181,11.181,11.181"
              transform="translate(945.021 2553.5) rotate(180)"
              fill="none"
              stroke="#0fa"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="4"
            />
          </g>
        </svg>
        <Trans
          i18nKey={"translations:sections_title.contacts"}
          components={[<span className="text-neon" />]}
        />
      </h5>
      <div className="section-content contact-content container">
        <div className="section-subtitle">{t("feedback")}</div>
        <form onSubmit={handleSubmit} className="form">
          <input
            type="text"
            autoComplete="off"
            name="name"
            className="form-control"
            value={name}
            placeholder={t("placeholders.name")}
            onChange={handleChangeName}
            required
          />
          <textarea
            name="message"
            autoComplete="off"
            id="message"
            value={message}
            className="form-control"
            placeholder={t("placeholders.message")}
            onChange={handleChangeMessage}
            required
          ></textarea>
          <input
            type="text"
            autoComplete="off"
            name="capt"
            value={captcha}
            className="form-control"
            placeholder={`${a}+${b}=?*`}
            onChange={handleChangeCapt}
            required
          />

          <div className="form-tooltip">
            {a} + {b} =
          </div>
          <button type="submit" className="btn btn-contacts">
            {t("send")}
          </button>
          <div className="row contacts-icon justify-content-between pt-3">
            <Link to="#" className="contacts-icon-link" title="Telegram">
              <i className="fab fa-telegram"></i>
            </Link>
            <Link to="#" className="contacts-icon-link" title="Instagram">
              <i className="fab fa-instagram"></i>
            </Link>
            <Link to="#" className="contacts-icon-link" title="Codepen">
              <i className="fab fa-codepen"></i>
            </Link>
          </div>
        </form>
      </div>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        shouldCloseOnEsc={true}
        shouldCloseOnOverlayClick={true}
        style={customStyles}
        contentLabel="Example Modal"
        onAfterOpen={() => {
          timer = setTimeout(() => {
            closeModal();
          }, 2500);
        }}
      >
        <h3>{modalText}</h3>
      </Modal>
    </section>
  );
}

export default Contacts;
