import React, { useRef } from "react";
// import { Link } from "react-router-dom";
import classnames from "classnames";
import { useTranslation } from "react-i18next";

interface Props {
  isOpenedMenu: boolean | null;
  handleOpeningMenu: () => void;
}

function Menu({ isOpenedMenu, handleOpeningMenu }: Props) {
  const { t } = useTranslation();
  const menu = useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    if (menu.current) {
      const duration =
        parseFloat(window.getComputedStyle(menu.current).animationDuration) *
        1000;
      if (!isOpenedMenu) {
        setTimeout(() => {
          menu.current?.classList.remove("hide");
        }, duration);
      }
    }
  }, [isOpenedMenu]);

  return (
    <div
      id="menu"
      ref={menu}
      role="navigation"
      className={classnames({
        show: isOpenedMenu,
        hide: isOpenedMenu === false,
      })}
    >
      <a
        href="#header"
        onClick={handleOpeningMenu}
        className="nav-link"
        role="menuitem"
      >
        {t("home")}
      </a>
      <a
        href="#about"
        onClick={handleOpeningMenu}
        className="nav-link"
        role="menuitem"
      >
        {t("about_me")}
      </a>
      <a
        href="#skills"
        onClick={handleOpeningMenu}
        className="nav-link"
        role="menuitem"
      >
        {t("my_skills")}
      </a>
      <a
        href="#portfolio"
        onClick={handleOpeningMenu}
        className="nav-link"
        role="menuitem"
      >
        {t("my_portfolio")}
      </a>
      <a
        href="#contacts"
        onClick={handleOpeningMenu}
        className="nav-link"
        role="menuitem"
      >
        {t("contacts")}
      </a>
    </div>
  );
}

export default Menu;
